  async asyncData({ store, params }) {
    await Promise.all([
      store.dispatch("teacher/getMeetingDetailData", params),
      store.dispatch("teacher/getClassDetailData", params.id),
      store.dispatch("teacher/getQuizDetailData", params),
            
