              this.$router.push(
                "/teacher/classes/class/" +
                  this.$store.state.teacher.classDetail.id+ "/meeting/"+ this.$store.state.teacher.meetingDetail.id
              );
            })
            .catch((error) => {
              this.closeDialog();
              this.text = error;
              this.snackbar = true;
            });
        } else {
          this.closeDialog();
        }
      });
